
.advantages {
    background-color: $light-bg;
    padding-top: 20px;
    padding-bottom: 70px;

    @include for-tablet {
        padding-top: 33px;
    }

    @include for-desktop {
        padding-top: 73px;
    }
}

.advantages__item {
    &:not(:last-child) {
        padding-bottom: 38px;
    }
}

.advantages__text {
    font-weight: 700;
    font-size: 12px;
    line-height: 1.7;
        
    color: $main-text-color;

    @include for-tablet {
        width: 180px;
        height: 67px;
    }

    @include for-desktop {
        width: 330px;
        font-size: 16px;
        line-height: 28px;
    }
}

.advantages__number {
    margin-bottom: 15px;

    font-family: $title-font;
    font-size: 36px;
    line-height: 1.13;
    letter-spacing: 0.04em;

    color: $accent-dark;


    @include for-desktop {
        font-size: 58px;
        line-height: 66px;
      }
}

.advantages__item--first::before {
    display: block;
    content: "";
    width: 33px;
    height: 50px;
    margin-bottom: 15px;
    background-image: url('../images/mobile/advantages/icon1_mobile.png');
    background-repeat: no-repeat;

    @include for-desktop {
        margin-bottom: 40px;
      }
}

.advantages__item--second::before {
    display: block;
    content: "";
    width: 50px;
    height: 50px;
    margin-bottom: 15px;
    background-image: url('../images/mobile/advantages/icon2_mobile.png');
    background-repeat: no-repeat;

    @include for-desktop {
        margin-bottom: 40px;
      }
}

.advantages__item--third::before {
    display: block;
    content: "";
    width: 46px;
    height: 50px;
    margin-bottom: 15px;
    background-image: url('../images/mobile/advantages/icon3_mobile.png');
    background-repeat: no-repeat;

    @include for-desktop {
        margin-bottom: 40px;
      }
}